<template>
  <div id="app">
    <header>
      <h1>Bar-o-matic-9000</h1>
    </header>
    <nav>
      <button
        class="random"
        :disabled="barList.length===0"
        @click="getRandomBar"
      >{{barList.length===0 ? "Loading..." : "Roll the Dice"}}</button>
      <div class="visited-container">
        <input type="checkbox" v-model="removeBeenHere" /> Remove visited
      </div>
    </nav>
    <BarContainer v-if="chosenBar" :details="chosenBar" />
  </div>
</template>

<script>
import getSheetsData from "./lib/get-sheets-data";
import BarContainer from "./components/BarContainer";

export default {
  name: "app",
  components: { BarContainer },
  data() {
    return {
      barList: [],
      removeBeenHere: false,
      chosenBar: null
    };
  },
  mounted() {
    getSheetsData(process.env.VUE_APP_SHEET)
      .then(res => {
        this.barList = res;
      })
      .catch(err => {
        console.log(err);
      });
  },
  computed: {
    filteredBarList() {
      let data = this.barList;
      for (var i = 0, f = data.length; i < f; i++) {
        data[i].originalIndex = i;
      }
      data = data.filter(n => !n.ignore);
      if (this.removeBeenHere) {
        data = data.filter(n => !n.beenThere);
      }
      return data;
    }
  },
  methods: {
    getRandomBar() {
      let randNum = Math.floor(Math.random() * this.filteredBarList.length);
      this.chosenBar = JSON.parse(
        JSON.stringify(this.filteredBarList[randNum])
      );

      //Remove this element from the original array to improve entropy on successive dice rolls
      this.barList.splice(this.chosenBar.originalIndex, 1);
    }
  }
};
</script>

