<template>
  <div>
    <img v-for="index in wholeStars" :key="index" src="star.png" />
    <img v-show="!halfStars" src="star-half.png" />
  </div>
</template>

<script>
export default {
  name: "StarRating",
  props: ["rating"],
  computed: {
    wholeStars() {
      return Math.floor(this.rating);
    },
    halfStars() {
      return Number.isInteger(Number(this.rating));
    }
  }
};
</script>

<style scoped>
img {
  max-width: 32px;
  margin:0 2px;
}
</style>