<template>
  <div class="bar-details-container" :style="`background-image:url(${details.image_url});`">
    <div class="details">
      <div class="name-container">{{details.name}}</div>
      <div class="address-container">
        <div v-for="(item, index) in newLineToArray(details.address)" :key="index">{{item}}</div>
      </div>

      <div>{{details.price}}</div>
      <StarRating :rating="details.rating" />
      <div class="category-container">
        <span
          class="category"
          v-for="(item, index) in newLineToArray(details.categories)"
          :key="index"
        >{{item}}</span>
      </div>
      <div class="link-container">
        <span class="anchor">
          <a :href="details.url" target="_blank">Yelp</a>
        </span> |
        <span class="anchor">
          <a :href="googleSearchQuery(details.name)" target="_blank">Google</a>
        </span>
      </div>
      <div v-if="details.beenThere">
        <span class="checkmark">✔</span> Been here
      </div>
    </div>
  </div>
</template>

<script>
import StarRating from "./StarRating";

export default {
  name: "BarDetails",
  components: { StarRating },
  props: ["details"],
  methods: {
    newLineToArray(prop) {
      return prop.split("\n").map(n => n.trim());
    },
    googleSearchQuery(name) {
      return `https://google.com/search?q=${encodeURIComponent(name)}`;
    }
  }
};
</script>

